img {
    width: 40px;
}
nav {
    position: fixed;
    background: linear-gradient(to bottom, rgb(13, 132, 134),rgb(13, 151, 147));
    right: 0;
    height: 60px;
    left: 0;
    z-index: 1030;
    color: white;
}
#tombolanav {
    border-color: none;
}
.nav-link {
    margin: 5px;
    outline: none;
    border-radius: 5px;
    text-align: center;
    padding: 10px;
}
.wrap {
    width: 100px;
    background: linear-gradient(to bottom, rgb(12, 36, 53),  rgb(9,83,133));
    height: 500px;
}
.wrap img{
    border: 3px solid rgb(250, 250, 250);
    width: 200px;
    text-align: center;
}


@media (max-width: 768px) {
    .wrap {
        background: linear-gradient(to bottom, rgb(10, 189, 199),  rgb(16, 202, 174),  rgb(7, 175, 136));
        width: 300px;
    }
    .nav-link {
        background-color: rgb(35, 145, 165);
        margin: 5px;
        outline: none;
        border-radius: 5px;
        text-align: center;
    }
    .nav-link:active,
    .nav-link:hover {
        background: linear-gradient(to bottom, rgb(49, 182, 177),  rgb(30, 147, 158));
        margin: 5px;
        outline: none;
        border-radius: 5px;
        text-align: center;
        cursor: pointer;
    }
    .wrap img{
        border: 3px solid rgb(250, 250, 250);
        width: 50%;
        text-align: center;
    }
    
}