/* content header */
.jumbotron{
    background: linear-gradient(to bottom, rgb(13, 132, 134),rgb(13, 151, 147));
    color: white;
}
.col-md-6 h1 {
    font-size: 62px;
    margin-top: 100px;
}
.txt-right {
    font-size: 30px;
    font-weight: bold;
    margin-top: 50px;
}
.txt-right2 {
    font-size: 16px;
    font-weight: normal;
    margin-top: -5px;
    margin-bottom: 5vh;
}
.Img-left{
    padding: 30px;
    }
/* content post */
.card img {
    height: 40%;
    width: 50%;
    margin-bottom: 20px;
}
.card {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;

}
.card:hover {
    background: linear-gradient(to bottom, rgb(28, 84, 85),rgb(13, 151, 147));
    color: rgb(255, 250, 250);
    align-items: center;
    transition: 1s;
    box-shadow: 5px 5px 5px rgb(6, 102, 104);
    border-radius: 10px;
}

/* end content post */
/* content mendaftar */
.col button {
    width: 200px;
    margin-top: 50px;
    /* margin: 20px; */
}
h4 {
    margin-top: 10vh;
    font-size: 40px;
}
/* button to top */
#myBtn  {
    position: fixed; /* Fixed/sticky position */
    bottom: 20px; /* Place the button at the bottom of the page */
    right: 30px; /* Place the button 30px from the right */
    z-index: 99; /* Make sure it does not overlap */
    border: none; /* Remove borders */
    outline: none; /* Remove outline */
    background-color: rgb(21, 104, 114); /* Set a background color */
    color: white; /* Text color */
    cursor: pointer; /* Add a mouse pointer on hover */
    padding: 5px; /* Some padding */
    border-radius: 10px; /* Rounded corners */
    font-size: 18px; /* Increase font size */
  }
  #myBtn:hover  {
    position: fixed; /* Fixed/sticky position */
    bottom: 20px; /* Place the button at the bottom of the page */
    right: 30px; /* Place the button 30px from the right */
    z-index: 99; /* Make sure it does not overlap */
    border: none; /* Remove borders */
    outline: none; /* Remove outline */
    background-color: rgb(24, 181, 202); /* Set a background color */
    color: white; /* Text color */
    cursor: pointer; /* Add a mouse pointer on hover */
    padding: 5px; /* Some padding */
    border-radius: 10px; /* Rounded corners */
    font-size: 18px; /* Increase font size */
  }
/*end button to top */

/* end content mendaftar */

@media (max-width: 768px) { 
    /* content header */
    .col-md-6 h1 {
        font-size: 70px;
        margin-top: 60px;
    }
    .txt-right {
        font-family: sans-serif;
        font-size: 24px;
        margin-bottom: 20px;
    }
    .txt-right2 p {
        font-family: sans-serif;
        font-size: 14px;
    }
    /* cnntent post */
    h5{
        font-weight: bold;
        font-family: 'Bree Serif', serif;
    }
    .title-blog {
        font-family: 'Bree Serif', serif;
        font-size: 30px;
        line-break: initial;
    }
    .card{
        padding-top: 10px;
        padding-bottom: 20px;
    }
    #myBtn {
        position: fixed; /* Fixed/sticky position */
        background-color: rgb(49,134,135);
        bottom: 7.7rem; /* Place the button at the bottom of the page */
        right: 20px; /* Place the button 30px from the right */
        z-index: 99; /* Make sure it does not overlap */
        border: none; /* Remove borders */
        outline: none; /* Remove outline */
        cursor: pointer; /* Add a mouse pointer on hover */
        border-radius: 10%; /* Rounded corners */
      }
      #myBtn:hover {
        position: fixed; /* Fixed/sticky position */
        background-color: rgb(0, 255, 128);
        bottom: 7.7rem; /* Place the button at the bottom of the page */
        right: 20px; /* Place the button 30px from the right */
        z-index: 99; /* Make sure it does not overlap */
        border: none; /* Remove borders */
        outline: none; /* Remove outline */
        cursor: pointer; /* Add a mouse pointer on hover */
        border-radius: 10%; /* Rounded corners */;
      }

 }