#navbarMenu{
    background: linear-gradient(to bottom, rgb(), rgb(6, 84, 136));
}
#prof-right{
    background: linear-gradient(to bottom, rgb(13, 132, 134),rgb(13, 151, 147));
}
#prof-right2{
    margin-top: 20px;
    align-items: center;
    padding: 50px;
}
#myProfile{
    background: linear-gradient(to bottom, rgb(45, 151, 148),  rgb(25, 114, 114));
}
.col-md img {
    width: 50%;
    border-radius: 50%;
    margin-bottom: 20px;
    border: 3px solid rgb(14, 112, 109);
    box-shadow: 5px 5px 10px rgb(7, 64, 85);
}
.col-md img:hover {
    width: 50%;
    border-radius: 50%;
    margin-bottom: 20px;
    border: 3px solid rgb(1, 170, 130)
}
.IconProfile {
    margin-top: 200px;
    width: 30px;
    height: 30px;
    margin: 5px;
}.IconProfile:hover {
    margin-top: 200px;
    width: 40px;
    height: 40px;
    margin: -5px;
    transition: 0.10s;
    transform-style: preserve-3d;
}

@media (max-width: 767.98px) { 
    #prof-right{
        background: linear-gradient(to bottom, rgb(21, 114, 116),rgb(13, 151, 147));
    }
    .col-md img {
        width: 60%;
        border-radius: 50%;
        margin-bottom: 20px;
        border: 4px solid rgb(255, 255, 255)
    }
    .col-md img:hover { 
        width: 60%;
        border-radius: 50%;
        margin-bottom: 20px;
        border: 4px solid rgb(5, 230, 255)
    }
    
 }